<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header backgroundNaviBar">
      <!-- <a v-if="openQuery" @click="Back"> -->
      <mu-button icon slot="left" class="icon" size="20" @click="Back">
        <mu-icon value="keyboard_arrow_left"></mu-icon>
      </mu-button>
      <!-- </a> -->
      <span :style="margin">调度列表</span>
      <mu-button
        icon
        slot="right"
        class="icon"
        size="20"
        @click="()=>{
            $router.push('/MonitoringDispatchingAdd')
        }"
      >
        <mu-icon value="add"></mu-icon>
      </mu-button>
      <mu-button
        icon
        slot="right"
        class="icon"
        size="20"
        ref="button"
        id="selectStation"
        @click="selectDataPic"
      >
        <img src="static/img/query.png" alt />
      </mu-button>
    </mu-appbar>
    <div class="cont">
      <div>
        <div class="contHeaderTitle">
          <div>调度时间</div>
          <div>调度人</div>
          <div>联系方式</div>
          <div>查看详情</div>
        </div>
        <div class="content" v-for="(item,index) in itemList" :key="index">
          <div>{{item.gmtDispatch}}</div>
          <div>{{item.vehiclePerson}}</div>
          <div>{{item.vehiclePhone}}</div>
          <div @click="clickTo(item.id)">
            <a>查看</a>
          </div>
        </div>
      </div>
    </div>
    <mu-popover placement="bottom" :open.sync="open" :trigger="trigger">
      <mu-list
        v-for="(item,index) in MenuList"
        :key="index"
        style="padding:0"
        class="btnCont"
        @click="BtnTo(item.name)"
      >
        <mu-list-item>
          <mu-list-item-title style="padding:0">{{item.name}}</mu-list-item-title>
        </mu-list-item>
      </mu-list>
    </mu-popover>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      margin: "margin-left: 48px;",
      itemList: [],
      token: "",
      user: "",
      vehicleNo: "",
      open: false,
      MenuList: [
        {
          name: "筛选"
        },
        {
          name: "新增调度"
        }
      ],
      trigger: null,
      openQuery: false
    };
  },
  methods: {
    Back() {
      this.$router.go(-1)
    },
    clickTo(id) {
      this.$router.push({
        path: "/MonitoringDispatchingDetails",
        query: {
          id: id
        }
      });
    },

    selectDataPic() {
      //   this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectStation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.vehicleList, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择车辆", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.vehicleNo = selectArr[0];
          //   _this.open = true;
          // var name = Echarts.switchData(selectArr[0]);
          _this.initData(name);
          // console.log(JSON.stringify(selectArr))
        }
      });
    },
    addToDisp() {},
    BtnTo(item) {
      switch (item) {
        case "筛选":
          this.selectDataPic();
          break;

        default:
          break;
      }
    },
    async initData() {
      var url = "/job/dispatch/list";
      var pageNum = 1;
      var pageSize = 1000000000;
      var vehicleNo = this.vehicleNo;
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize,
          vehicleNo
        },
        this.token
      );
      console.log(res);
      this.$store.commit("dispatchingData", res.data.data.rows);
      // AppBace.TimeFor(res.data.data.rows);
      this.itemList = await AppBace.TimeFor(res.data.data.rows);
    }
  },
  mounted() {
    this.trigger = this.$refs.button.$el;
    this.token = sessionStorage.getItem("token");
    this.user = sessionStorage.getItem("user");
    if (this.user === "station") {
      this.openQuery = true;
    }
    this.initData();
  }
};
</script>
<style scoped>
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.icon img {
  width: 24px;
  height: 24px;
}
.cont {
  margin-top: 60px;
  background: white;
}
.contHeaderTitle {
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid rgb(204, 204, 204);
}
.contHeaderTitle div {
  width: 25%;
  display: flex;
  justify-content: center;
  height: 40px;
  align-items: center;
}
.content {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid rgb(228, 227, 227);
}
.content div {
  width: 25%;
  display: flex;
  justify-content: center;
  height: 40px;
  align-items: center;
}
.btnCont {
  border-bottom: 1px solid #ccc;
}
</style>


